// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import Routes from "./routes";

Vue.config.productionTip = false;
Vue.use(VueResource);
Vue.use(VueRouter);

// 自定义指令
// Vue.directive('rainbow',{//自定义rainbow 指令：改变showblogs中的h2 样式
//   bind(el,binding,vnode){//el代表元素，
//     el.style.color = "#"+Math.random().toString(16).slice(2,8);//拼接彩虹颜色  #
//   }
// })
Vue.directive('theme',{//自定义theme指令：改变showblogs中de容器宽度以及背景色
  bind(el,binding,vnode){
    if(binding.value == 'wide'){
      el.style.maxWidth = "1260px";
    }else if (binding.value == 'narrow'){
      el.style.maxWidth = "560px";
    }
    if(binding.arg == 'column'){
      el.style.background = "#6677cc";
      el.style.padding = "20px";
    }
  }
});

//自定义过滤器
// Vue.filter("to-uppercase",function(value){//将所有标题改为大写
//   return value.toUpperCase();
// })

// Vue.filter("snippet",function (value) {//将文章中的内容实现缩短
//   return value.slice(0,100)+"...";
// })


// 创建路由

const router = new VueRouter({
  routes : Routes,
  mode:"history"//去掉路由中的 "#"
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router:router
});


//index.html  ->  main.js  -> app.vue



