<template>
  <div v-theme:column="'wide'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
<!--    <div v-for ="blog in blogs" class="single-blog">-->
    <div v-for ="blog in filteredBlogs" class="single-blog">
       <router-link v-bind:to="'/blog/'+blog.id">
         <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
       </router-link>
      <article>
        {{blog.content | snippet}}
<!--        {{blog.body | snippet}}-->
      </article>
    </div>

  </div>
</template>

<script>
    export default {
        name: 'show-blogs',
        data(){
            return{
                blogs:[],
                search:""
            }
        },
        created(){
            this.$http.get('https://vue-demo-94b2b.firebaseio.com/post.json')
                .then(function(data){//打印出来的是一个json对象
                    // console.log(data);
                    // console.log(data.json());
                    return data.json()
                    // this.blogs = data.body.slice(0,10);   //只取10条数据
                    // console.log(this.blogs);

                })
                .then(function(data){//拿到json对象，将它给到data来接受，1.将对象放到数组里面。2.再将数组赋给data里面的blogs
                    var blogsArray = [];//定义一个空数组
                    for(let key in data ){//把对象遍历
                        // console.log(key); //key ：每个对象的特有名称
                        // console.log(data[key])  // 打印每个对象内的数据内容
                        data[key].id = key;         //把每个对象的key绑定一个ID值
                        blogsArray.push(data[key])    //将每个对象都push到blogsArray空数组内
                    }
                    this.blogs = blogsArray;
                })

        },
        computed:{
            filteredBlogs:function(){//搜索过滤器
                return this.blogs.filter((blog)=>{
                    return blog.title.match(this.search)
                })
            }
        },
        filters:{
            // "to-uppercase":function(value){//将所有标题改为大写
            //     return value.toUpperCase();
            // },
            toUppercase(value){//将所有标题改为大写  ES6语法
                return value.toUpperCase();
            },
            "snippet":function (value) {//将文章中的内容实现缩短
                return value.slice(0,100)+"...";
            }
        },
        directives:{
            'rainbow':{//自定义rainbow 指令：改变showblogs中的h2 样式
                bind(el,binding,vnode){
                    el.style.color = "#"+Math.random().toString(16).slice(2,8);//拼接彩虹颜色  #
                }
            }
        }
    }
</script>

<style scoped>
  #show-blogs{
    max-width:800px;
    margin:0 auto;
  }
  .single-blog{
    padding:20px;
    margin:20px 0;
    box-sizing:border-box;
    background:#eee;
    border:1px dotted #aaa;
  }
  #show-blogs a{
    color:#444;
    text-decoration: none;
  }
  input[type="text"]{
    padding:8px;
    width:100%;
    box-sizing:border-box;
  }

</style>



